/**
 * Weather Widget
 *
 * I'm a Widget.
 * I can be placed into a Workspace that allows children (canHaveChildren = true)
 * I can also access the context for the workspace as seen in the example below
 * - Context is a great place to pass in libraries, apis, data, etc that need to be accessible in the Widget.
 * - my props are very interesting! If you configure userConfig, the keys will be the prop names and you can access them.
 *
 * @pacakge Weather
 */
import { useContext, useEffect } from "react";
import { Widget, Heading2, SubHeading3, Panel } from "@dash";
import { WeatherContext } from "../contexts";

export const WeatherWidget = ({
    title = "Hello",
    subtitle = "Im a widget.",
    listen,
    publishEvent,
    helpers,
    ...props
}) => {
    useEffect(() => {
        // console.log("weather widget use effect ", listen("listenEvent"));
        const listeners = {
            handleSearchChange: ["heardSomething"], //["CustomSearchbar[10].searchQueryChanged"],
        };
        const handlers = {
            heardSomething: heardSomething,
        };
        listen(listeners, handlers);
        publishEvent("name", { test: "test" });
    }, []);

    function heardSomething(data) {
        console.log("heard something ", data);
    }
    {
        /* Sample using the WeatherContext to show how to access the sampleClient */
    }
    const { sampleClient } = useContext(WeatherContext);
    {
        /* Note: We are using the Dashboard components (themed for your pleasure) to build this Widget render, you can use whatever you want! */
    }
    return (
        <Widget
            {...props}
            width={"w-full"}
            height={"h-full"}
            scrollable={false}
        >
            <Panel backgroundColor={"bg-green-500"}>
                <Panel.Header>
                    <Heading2 title={title} textColor={"text-white"} />
                </Panel.Header>
                <Panel.Body>
                    <SubHeading3
                        title={subtitle}
                        textColor={"text-gray-300"}
                        backgroundColor="none"
                    />
                </Panel.Body>
                <Panel.Footer>{sampleClient.foo()}</Panel.Footer>
            </Panel>
        </Widget>
    );
};
