import React, { useContext, useEffect, useState } from "react";
import {
    DashboardContext,
    Widget,
    Panel,
    WorkspaceContext,
    SubHeading3,
} from "@dash";
import { CustomHits } from "./CustomHits";
import { useSearchBox } from "react-instantsearch-hooks-web";
import { Index, Configure } from "react-instantsearch-hooks-web"; // use for single or multi index search

export const SimpleSearch = ({
    hitComponent = "SimpleSearchHit",
    cols = 3,
    neuralFilter = "",
    indexName = "",
    hitsPerPage = 30,
    page = 0,
    ...props
}) => {
    const { listeners, uuid } = props;
    const { widgetApi } = useContext(DashboardContext);
    const { workspaceData } = useContext(WorkspaceContext);
    const [currentPage, setCurrentPage] = useState(page);

    // use this to handle the listener
    const { refine } = useSearchBox(props);

    const handlers = {
        searchQueryChanged: searchQueryChanged,
        refinementsChanged: refinementsChanged,
        filtersChanged: filtersChanged,
        pageChanged: pageChanged,
    };

    useEffect(() => {
        if (widgetApi !== undefined && listeners) {
            widgetApi.registerListeners(listeners, handlers, uuid);
        }
    }, [widgetApi, listeners]);

    function searchQueryChanged(e) {
        refine(e.query);
    }

    function refinementsChanged(data) {}

    function pageChanged(page) {
        console.log("page changed in SimpleSearch ", page);
        setCurrentPage(parseInt(page.page));
    }

    function filtersChanged(filter) {
        console.log("neural filter listened ", filter);
        // setNeuralFilter(filter["filter"]);
    }

    function getHitsPerPage() {
        return workspaceData !== undefined &&
            workspaceData.hasOwnProperty("hitsPerPage")
            ? workspaceData["hitsPerPage"]
            : hitsPerPage !== null
            ? hitsPerPage
            : 20;
    }

    function getPage() {
        console.log("getting page ", workspaceData);
        const pageValue =
            workspaceData !== undefined && workspaceData.hasOwnProperty("page")
                ? workspaceData["page"]
                : page !== null
                ? page
                : null;

        console.log("page value: ", pageValue);
        return pageValue;
    }

    function getIndexName() {
        return indexName !== ""
            ? indexName
            : workspaceData !== undefined &&
              workspaceData.hasOwnProperty("indexName")
            ? workspaceData["indexName"]
            : "";
    }

    return (
        <Widget width={"w-full"} height={"h-full"} grow={true}>
            <Panel height={"h-full"}>
                <Panel.Header>
                    <SubHeading3 title={getIndexName()} padding={false} />
                </Panel.Header>
                <Panel.Body>
                    {getIndexName() !== "" && (
                        <Index
                            indexName={getIndexName()}
                            indexId={`index-${getIndexName()}`}
                        >
                            <Configure
                                getRankingInfo={true}
                                // facets={["*"]}
                                hitsPerPage={getHitsPerPage()}
                                page={2}
                            />
                            <CustomHits
                                hitComponent="CustomHitJCrew"
                                cols={cols}
                                neuralFilter={neuralFilter}
                            />
                        </Index>
                    )}
                    {/* {indexName === "" && (
            <CustomHits
              hitComponent="CustomHitJCrew"
              cols={cols}
              neuralFilter={neuralFilter}
              page={getPage()}
              hitsPerPage={getHitsPerPage()}
            />
          )} */}
                </Panel.Body>
            </Panel>
        </Widget>
    );
};
